import React, { useEffect, useRef, useState } from 'react';
import { Scene, Camera, WebGLRenderer, Group, Color, HemisphereLight } from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';

import SoundWave from 'components/SoundWave';
import SoundIcon from 'components/SoundIcon';

import { getMaxCameraResolution } from 'helpers/cameraResolution';

const AR = props => {
  const { initialScale, patternUrl, cameraParametersUrl, modelUrl, audioUrl } = props;
  const audioRef = useRef();
  const anchorGltf = useRef();
  const [scale, setScale] = useState(initialScale);
  const [isMute, setIsMute] = useState(false);
  const [isMarkerFound, setIsMarkerFound] = useState(false);
  const [cameraResolution, setCameraResolution] = useState(null);

  useEffect(() => {
    getMaxCameraResolution()
      .then(setCameraResolution);
  }, [])

  useEffect(() => {
    if (anchorGltf.current) {
      anchorGltf.current.scene.scale.set(scale, scale, scale);
    }
  }, [scale]);

  useEffect(() => {
    console.log({ cameraResolution });
    if (!cameraResolution) {
      return;
    }

    let isMounted = true;
    let isMarkerVisible = false;
    const { width, height } = cameraResolution;
    const { ArToolkitSource, ArToolkitContext, ArMarkerControls } = window.THREEx;
    const scene = new Scene();
    const camera = new Camera();
    const renderer = new WebGLRenderer({
      antialias: true,
      alpha: true
    });
    const light = new HemisphereLight(0xffffbb, 0x080820, 5);
    const arToolkitSource = new ArToolkitSource({
      sourceType: 'webcam',
      sourceWidth: width,
      sourceHeight: height,
      displayWidth: width,
      displayHeight: height
    });
    const arToolkitContext = new ArToolkitContext({
      cameraParametersUrl,
      detectionMode: 'mono',
      canvasWidth: width,
      canvasHeight: height

    });
    const markerRoot = new Group();

    const onResize = () => {
      arToolkitSource.onResizeElement();
      arToolkitSource.copyElementSizeTo(renderer.domElement);
      if (arToolkitContext.arController !== null) {
        arToolkitSource.copyElementSizeTo(arToolkitContext.arController.canvas);
      }
    }

    const initialize = () => {
      scene.add(light);
      scene.add(camera);
      renderer.setClearColor(new Color('lightgrey'), 0)
      renderer.setSize(width, height);
      renderer.domElement.style.position = 'absolute';
      renderer.domElement.style.top = '0px';
      renderer.domElement.style.left = '0px';
      document.getElementById('ar-container').appendChild(renderer.domElement);
    
      arToolkitSource.init(onResize);

      window.addEventListener('resize', onResize);

      arToolkitContext.init(() => {
        camera.projectionMatrix.copy(arToolkitContext.getProjectionMatrix());
      });

      scene.add(markerRoot);

      new ArMarkerControls(arToolkitContext, markerRoot, {
        type: 'pattern',
        patternUrl
      })

      function onProgress(xhr) {
        console.log((xhr.loaded / xhr.total * 100) + '% loaded');
      }

      function onError(error) {
        console.log('An error happened:', error);
      }

      new GLTFLoader()
        .load(modelUrl, gltf => {
          anchorGltf.current = gltf;

          console.log(gltf);
          anchorGltf.current.scene.scale.set(initialScale, initialScale, initialScale);
          markerRoot.add(anchorGltf.current.scene);
        }, onProgress, onError);
    }

    const update = () => {
      if (isMarkerVisible !== markerRoot.visible) {
        setIsMarkerFound(markerRoot.visible);
        isMarkerVisible = markerRoot.visible;

        if (markerRoot.visible) {
          audioRef.current.play();
        } else {
          audioRef.current.pause();
        }
      }
      if (markerRoot.visible) {
        anchorGltf.current.scene.rotation.y += 0.01;
      }
      if (arToolkitSource.ready !== false) {
        arToolkitContext.update(arToolkitSource.domElement);
      }
    };

    const render = () => {
      renderer.render(scene, camera);
    };
    
    
    const animate = () => {
      if (isMounted) {
        requestAnimationFrame(animate);
        update();
        render();
      }
    };

    initialize();
    animate();

    return () => {
      isMounted = false;
      arToolkitSource.domElement.srcObject.getTracks()
        .forEach(track => track.stop());
      document.getElementById('ar-container').removeChild(arToolkitSource.domElement);
      document.getElementById('ar-container').removeChild(renderer.domElement);
    };
  }, [initialScale, cameraParametersUrl, patternUrl, modelUrl, cameraResolution]);

  return (
    <div>
        <audio
          loop
          ref={audioRef}
          preload='auto'
        >
          <source
            src={audioUrl}
            type='audio/mpeg'
          />
        </audio>
        <button
          style={{
            position: 'fixed',
            zIndex: 3,
            right: 10,
            top: 10,
            background: 'none',
            border: 'none',
            padding: 3,
            display: 'inline-flex'
          }}
          onClick={() => {
            setIsMute(!isMute);
            audioRef.current.volume = isMute ? 1 : 0;
          }}
        >
          <SoundIcon isMute={isMute} />
        </button>
        <SoundWave
          isActive={isMarkerFound}
          style={{
            position: 'fixed',
            zIndex: 3,
            right: 45,
            top: 10,
            background: 'none',
            border: 'none',
            padding: 3,
            display: 'inline-flex'
          }}
        />
        <button
          style={{
            position: 'fixed',
            zIndex: 3,
            width: 30,
            height: 30,
            right: 10,
            bottom: 50,
            background: 'none',
            padding: 3,
            display: 'inline-flex',
            color: '#fff',
            fontSize: 24,
            border: '1px solid #fff',
            borderRadius: 3,
            alignItems: 'center',
            justifyContent: 'center'
          }}
          onClick={() => {
            setScale(prevScale => prevScale + 0.0001);
          }}
        >
          +
        </button>
        <button
          style={{
            position: 'fixed',
            zIndex: 3,
            width: 30,
            height: 30,
            right: 10,
            bottom: 10,
            background: 'none',
            padding: 3,
            display: 'inline-flex',
            color: '#fff',
            fontSize: 24,
            border: '1px solid #fff',
            borderRadius: 3,
            alignItems: 'center',
            justifyContent: 'center'
          }}
          onClick={() => {
            setScale(prevScale => Math.max(0.001, prevScale - 0.0001));
          }}
        >
          -
        </button>
    </div>
  );
};

AR.defaultProps = {
  initialScale: 0.004,
  audioUrl: `${process.env.PUBLIC_URL}/audio/anchor.mp3`,
  modelUrl: `${process.env.PUBLIC_URL}/models/anchor/MiraiKoukai1.gltf`,
  patternUrl: `${process.env.PUBLIC_URL}/data/hiro.patt`,
  cameraParametersUrl: `${process.env.PUBLIC_URL}/data/camera_para.dat`
};

export default AR;
